<template>
	<div id="goodsClassify">
    <div class="header">
      <div class="van-hairline--bottom van-nav-bar">
        <div class="van-nav-bar__left">
          <i class="van-icon van-icon-arrow van-nav-bar__arrow" @click="$router.back(-1)" style="color: #000000 !important;"></i>
        </div>
        <div class="van-ellipsis van-nav-bar__title">商品分类</div>
      </div>
    </div>
    <div class="login_box">
      <van-badge-group :active-key="activeKey" @change="onChange">
        <!--<van-badge title="全部" @click="onChange(0)"/>-->
        <van-badge :title="item.classaname" @click="onChange(item)" v-for="(item,index) in classifyList"/>
      </van-badge-group>
      <div style="width: 70%;background-color: #ffffff;position: absolute;right: 0;top: 46px;height: 100%;">
        <div @click="toGoodsList(item)" style="width: 31.5%;display: inline-block;text-align: center;padding: 2px" v-for="(item,index) in secondClassifyList" :key="index">
          <span style="position: relative;top: 70px;color: rgb(83, 179, 16);">{{item.classbname}}</span>
          <img :src="item.classbimg" style="height: 80px;width: 80px;">
        </div>
      </div>
    </div>
	</div>
</template>
<script>
	export default {
		name: 'goodsClassify',
		data() {
			return {
        activeKey: 0,
        userId:localStorage.getItem('userId'),
        attributeid:'',
        classifyList:[],
        secondClassifyList:[],
			}
		},
    mounted() {
      this.attributeid = this.$store.state.goodsClassify.id;
      this.activeKey = this.$store.state.goodsClassify.index;
      this.getClassifyList();
    },
		methods:{
      toGoodsList(dataMap){
        this.$store.commit('setGoodsList', dataMap);
        this.$router.push('goodsList');
      },
      getClassifyList(){
        let $this = this;
        let obj = {
          attributeid: $this.attributeid,
        };
        let qs = require('qs');
        $this.$ajax.post("classa/getclassalist", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            for(let i=0;i<rpdata.data.data.length;i++){
              rpdata.data.data[i].classifyIndex = i;
            }
            $this.classifyList = rpdata.data.data;
            console.log($this.classifyList);
            $this.getSecondClassifyList($this.classifyList[$this.activeKey].classaid);
          }
        })
        .catch(function(err) {})
      },
      getSecondClassifyList(dataMap){
        let $this = this;
        let obj = {
          classaid: dataMap,
        };
        let qs = require('qs');
        $this.$ajax.post("classb/getclassblist", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.secondClassifyList = rpdata.data.data;
          }
        })
        .catch(function(err) {})
      },
      onChange(key) {
        this.activeKey = key.classifyIndex;
        let goodsClassify = {
          id:this.attributeid,
          index:key.classifyIndex
        };
        this.$store.commit('setGoodsClassify', goodsClassify);
        this.getSecondClassifyList(key.classaid);
      }
		}
	}
</script>
<style scoped>
  .van-badge-group {
    width: 30%;
  }
  .van-badge--select {
    font-weight: 500;
    color: #c10804 !important;
    border-color: #c10804;
  }
  .van-badge{
    text-align: center;
    border-bottom: 1px solid #eeeeee;
    color: #8b8b8b;
  }
  .login_box{
    margin-top: 44px;
  }
</style>



